@import url(https://fonts.googleapis.com/css2?family=Newsreader:wght@300&family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  padding: 0;

  font-family: "Newsreader", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.main-intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  padding: 50px 20px 20px 20px;
}
.inner-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  text-align: center;
}

#subtitle {
  font-size: 1rem;
  color: #a5b4fc;
}

.my-img {
  height: 300px;
  width: 300px;

  max-width: 100%;
  align-self: center;
}
.skill-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
  pointer-events: none;
}
.skill-grid:hover > .skill:hover {
  opacity: 1;
  transform: scale(1.05);
  color: white;
  background-color: #8b5cf6;
  transition: 0.45s transform ease;
  -webkit-filter: none;
          filter: none;
}
.skill-grid:hover > .skill {
  opacity: 0.4;
  -webkit-filter: blur(1px);
          filter: blur(1px);
}
.skill {
  padding: 30px;
  background-color: #f9f9ff;
  text-align: center;
  border-radius: 10px;
  border: 1px dotted #90acd1;
  pointer-events: auto;
}

@media screen and (max-width: 1024px) {
  .skill-grid {
    grid-template: repeat(6, 1fr) / 1fr;
  }
  .skill-title {
    text-align: center;
  }
  .my-img {
    margin-top: 1rem;
  }
  .main-intro {
    padding-top: 0;
  }
}

.title {
  font-size: 5rem;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  color: #6b7280;
}

.text {
  color: #8b5cf6;
}
.alignContent {
  padding: 0rem 1rem;
}
.center-section {
  background-color: #f3f4f6;
  padding: 1rem;
  border-bottom-right-radius: 1rem;
  margin-top: 1rem;
}
.links {
  padding: 0.5rem;
  display: block;
}
.btn {
  padding: 1rem 2rem;
  outline: 0 !important;
  border-radius: 30px;
}

.purple-btn {
  color: white;
  background-color: #8b5cf6;
  border: 1px transparent solid;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 14px;
  position: relative;
}
.purple-btn:hover {
  border-color: #8b5cf6;
  background-color: white;
  color: #8b5cf6;
  transition: 0.6s ease;
  z-index: 1;
}
.purple-btn:before,
.purple-btn::after {
  height: 20px;
  width: 20px;
  content: "";
  background-color: white;
  position: absolute;
  top: 50%;
  border-radius: 50%;
  z-index: -1;
}
.purple-btn:before {
  left: -30px;
  transform: translate(-50%, -50%);
}
.purple-btn:after {
  right: -30px;
  transform: translate(50%, -50%);
}
.purple-btn:hover::before {
  -webkit-animation: toRight 0.45s forwards;
          animation: toRight 0.45s forwards;
}
.purple-btn:hover::after {
  -webkit-animation: toLeft 0.45s forwards;
          animation: toLeft 0.45s forwards;
}
.gradient-text{
  --bg-size: 400%;
  --color-one: rgb(147, 37, 244);
  --color-two: rgba(156, 144, 237, 0.239);
  font-family: sans-serif;
  background: linear-gradient(
      90deg,
      var(--color-one),
      var(--color-two),
      var(--color-one)
    )
    0 0 / var(--bg-size) 100%;
  color: transparent;
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-animation: move-bg 8s infinite linear;
          animation: move-bg 8s infinite linear;
}
@-webkit-keyframes move-bg{
to{
  background-position:var(--bg-size) 0 ;
}
}
@keyframes move-bg{
to{
  background-position:var(--bg-size) 0 ;
}
}
@-webkit-keyframes toRight {
  0% {
    left: -30px;
  }
  50% {
    left: 50%;
    height: 20px;
    width: 20px;
  }
  100% {
    left: 50%;
    height: 100%;
    width: 100%;
    border-radius: inherit;
  }
}
@keyframes toRight {
  0% {
    left: -30px;
  }
  50% {
    left: 50%;
    height: 20px;
    width: 20px;
  }
  100% {
    left: 50%;
    height: 100%;
    width: 100%;
    border-radius: inherit;
  }
}

@-webkit-keyframes toLeft {
  0% {
    right: -30px;
  }
  50% {
    right: 50%;
    height: 20px;
    width: 20px;
  }
  100% {
    right: 50%;
    height: 100%;
    width: 100%;
    border-radius: inherit;
  }
}

@keyframes toLeft {
  0% {
    right: -30px;
  }
  50% {
    right: 50%;
    height: 20px;
    width: 20px;
  }
  100% {
    right: 50%;
    height: 100%;
    width: 100%;
    border-radius: inherit;
  }
}
.sub-title {
  font-size: 2rem;
}
@media screen and (max-width: 1024px) {
  .sub-title {
    line-height: 3rem;
  }
}

.main-about {
  display: flex;
  margin-top: 2rem;
  justify-content: center;
  padding: 20px;
  background-color: #F3F4F6  ;

}

.icon-sitting {
  height: 300px;
  width: 300px;
  border-radius: 50%;
  max-width: 100%;
  align-self: center;
  transform: scaleX(-1);
}
.about-info {
  padding: 2rem;
  line-height: 2rem;
}
@media screen and (max-width: 1024px) {
  .main-about {
    flex-wrap: wrap;
    flex-direction: column;
  }
}

.work-main {
  display: flex;
  flex-direction: column;
  padding: 0 20px;
  text-align: left;
}

.card-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
}
.card {
  border: 1px solid;
  line-height: 1.5rem;
  padding: 10px 30px;
  background-color: #f9f9ff;
  border-radius: 10px;
  border: 1px solid #818cf8;
  transition: all 0.4s;
  position: relative;
  overflow: hidden;

}
.card p{
  margin: 0;
  padding: 10px 0px;
}
.card h2{
  margin:10px 0px;
}
.card:hover {
  border: none;
  color: white;
}
.card:hover p {
  z-index: 1;
}
.card::before {
  content: "";
  position: absolute;
  top: 60px;
  left: 15px;
  width: 5px;
  height: 195px;
  border-radius: 50px;
  transition: all 0.4s;
  background: linear-gradient(19deg, #21d4fd 0%, #8721ff 74%);
}

.card:hover::before {
  top: 0;
  left: 0;
  border-radius: 10px;
  width: 100%;
  height: 100%;
}
.card p {
  position: relative;
}
.card:hover p {
  color: white;
}
.row-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
.work-btn {
  position: absolute;
  right: 0;
  margin: 0;
  background-color: white;
  border: 1px solid #818cf8;
}
@media screen and (max-width: 1024px) {
  .card-container {
    grid-template: repeat(3, 1fr) / 1fr;
  }
}
@media screen and (max-width: 1280px) {
  .row-container {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
  }

  .work-btn {
    position: unset;
  }
}

.contact-main {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.icon-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin-top: 5px;
}
.icon {
  margin: 5px;
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}
.web-menu {
  display: flex;
  background-color: #8b5cf6;
  position: fixed;
  width: 100%;
  padding: 1rem 0rem;
  top: 0;
  left: 0;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  border-bottom-left-radius: 1rem;
}
.nav-btn {
  margin-left: 30px;
  outline: 0;
  background-color: transparent;
  border: none;
  font-size: 24px;
  font-weight: bold;
  color: aliceblue;
  position: relative;
}
.nav-btn::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: white;
  transition: transform 0.4s ease;
  transform-origin: bottom right;
  transform: scaleX(0);
}
.nav-btn:hover::after {
  transform-origin: bottom left;
  transform: scaleX(1);
}
.container {
  display: flex;
  flex-direction: column;
  position: relative;
}

@media screen and (max-width: 767px) {
  .web-menu {
    display: none;
  }
}

