.main-intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  padding: 50px 20px 20px 20px;
}
.inner-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  text-align: center;
}

#subtitle {
  font-size: 1rem;
  color: #a5b4fc;
}

.my-img {
  height: 300px;
  width: 300px;

  max-width: 100%;
  align-self: center;
}
.skill-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  pointer-events: none;
}
.skill-grid:hover > .skill:hover {
  opacity: 1;
  transform: scale(1.05);
  color: white;
  background-color: #8b5cf6;
  transition: 0.45s transform ease;
  filter: none;
}
.skill-grid:hover > .skill {
  opacity: 0.4;
  filter: blur(1px);
}
.skill {
  padding: 30px;
  background-color: #f9f9ff;
  text-align: center;
  border-radius: 10px;
  border: 1px dotted #90acd1;
  pointer-events: auto;
}

@media screen and (max-width: 1024px) {
  .skill-grid {
    grid-template: repeat(6, 1fr) / 1fr;
  }
  .skill-title {
    text-align: center;
  }
  .my-img {
    margin-top: 1rem;
  }
  .main-intro {
    padding-top: 0;
  }
}
